body .select2-container.atum-select2, body .select2-container.atum-enhanced-select {
  font-size: 14px;
  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  position: relative;
  vertical-align: middle;
}

body .select2-container.atum-select2 .select2-results, body .select2-container.atum-enhanced-select .select2-results {
  display: block;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
  max-height: inherit;
  padding: 0;
  margin: 0;
  position: relative;
  overflow-x: inherit;
  overflow-y: inherit;
}

body .select2-container.atum-select2 .select2-results .select2-results__option, body .select2-container.atum-enhanced-select .select2-results .select2-results__option {
  padding: 8px 10px;
}

body .select2-container.atum-select2:focus, body .select2-container.atum-select2 *:focus, body .select2-container.atum-enhanced-select:focus, body .select2-container.atum-enhanced-select *:focus {
  outline: none;
}

body .select2-container.atum-select2:focus .select2-selection, body .select2-container.atum-enhanced-select:focus .select2-selection {
  box-shadow: 0 0 2px rgba(91, 157, 217, 0.8);
  border-color: #5B9DD9;
}

body .select2-container.atum-select2 .select2-selection__arrow b, body .select2-container.atum-enhanced-select .select2-selection__arrow b {
  display: none;
}

body .select2-container.atum-select2 .select2-selection__arrow:after, body .select2-container.atum-enhanced-select .select2-selection__arrow:after {
  line-height: 27px;
  font-family: atum-icon-font;
  content: "";
  font-size: 10px;
  color: var(--atum-table-filter-dropdown);
}

body .select2-container.atum-select2 .select2-selection--single, body .select2-container.atum-enhanced-select .select2-selection--single {
  box-sizing: border-box;
  cursor: pointer;
  display: block;
  height: 28px;
  user-select: none;
}

body .select2-container.atum-select2 .select2-selection--single .select2-selection__rendered, body .select2-container.atum-enhanced-select .select2-selection--single .select2-selection__rendered {
  display: block;
  padding-left: 8px;
  padding-right: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

body .select2-container.atum-select2 .select2-selection--single .select2-selection__clear, body .select2-container.atum-enhanced-select .select2-selection--single .select2-selection__clear {
  position: relative;
  visibility: hidden;
