  background-color: var(--atum-table-bg) !important;
  border: 1px solid var(--gray-500);
  border-radius: 5px;
  overflow: hidden;
  box-sizing: border-box;
  display: block;
  position: absolute;
  left: -100000px;
  width: 100%;
  z-index: 100000;
}

